@import url(https://fonts.googleapis.com/css2?family=Antonio&family=Spartan&display=swap);
.body{
  font-family: 'Spartan', sans-serif;
  background-color: #070724;
}

.main{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 100px 3fr 1fr;
    grid-template-areas: 
    "header header header header"
    "info info info info"
    "data data data data";

    grid-row-gap: 80px;

    row-gap: 80px;
}


.info{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 150px 48px 48px 48px;
    grid-row-gap: 25px;
    row-gap: 25px;
    grid-area: stats;
  
}

.middle{
    grid-area: info;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 500px;
    grid-template-areas: 
    "img stats";
    grid-column-gap: 100px;
    -webkit-column-gap: 100px;
            column-gap: 100px;

}

.title{
    font-family: Antonio;
    font-style: normal;
    font-weight: normal;
    font-size: 80px;
    line-height: 104px;
    /* identical to box height */

    text-transform: uppercase;
    
    color: #FFFFFF;
    min-width: 350px;
}

.summary{
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 25px;
    /* or 179% */
    color: #FFFFFF;
    max-width: 350px;

}

.info button{
    width: 350px;
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    line-height: 25px;
    letter-spacing: 2.57143px;
    text-transform: uppercase;
    color: #FFFFFF;
    border: 1px solid rgba(255,255, 255, .5);
    box-sizing: border-box;
    justify-items: end;
    background-color: Transparent;
}

.info button:hover{
    background-color: rgba(255,255, 255, .3);
   
}

.info button:focus{
    background-color: blue;
}

.planet-img{
    grid-area: img;
    align-self: center;
    justify-self: end;
    position: relative;
}

.zoom {
    position: absolute;
    width: 163px;
    height: 199px;
    top: 80%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    display: none;
}



.stats{
    grid-area: data;
    display: grid;
    grid-template-columns: 255px 255px 255px 255px;
    -webkit-align-items: center;
            align-items: center;
    justify-self: center;
    grid-gap: 20px;
    gap: 20px;
}

.data_title{
    font-style: normal;
    font-weight: bold;
    font-size: 11px;
    line-height: 25px;
    /* identical to box height, or 227% */

    letter-spacing: 1px;
    text-transform: uppercase;

    color: #FFFFFF;

    mix-blend-mode: normal;
    opacity: 0.5;
}


.main-info {
    font-family: 'Antonio', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 40px;
    line-height: 52px;
    /* identical to box height */

    letter-spacing: -1.5px;
    text-transform: uppercase;

    color: #FFFFFF;
    
}

header{
    grid-area: header;
    display: grid;
    grid-template-columns: 1fr 100px 100px 100px 100px 100px 100px 100px 100px; 
    margin: 10px 120px;
}

header button{
    font-family: 'Spartan', sans-serif;
    font-weight: bold;
    font-size: 11px;
    line-height: 25px;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: white;
    background-color: Transparent;
    justify-self: end;
    border: none;
    -webkit-align-self: center;
            align-self: center;  
    
}



.home{
    font-family: 'Antonio', sans-serif;
    justify-self: start;
    font-size: 28px;
}

